import * as React from 'react';
import { FormGroup } from "../components/pretty/formgroup";

class RegisterProps{

}

class RegisterState{

}

export class Register extends React.Component<RegisterProps, RegisterState>{

    render(){
        return(
            <div>
                <h1>Register</h1>
                <FormGroup id="username" label="username" type="text"></FormGroup>
                <FormGroup id="first-name" label="first name" type="text"></FormGroup>
                <FormGroup id="surname" label="surname" type="text"></FormGroup>
                <FormGroup id="email" label="email" type="email"></FormGroup>
                <FormGroup id="password" label="password" type="password"></FormGroup>
                <FormGroup id="confirm-password" label="confirm password" type="password"></FormGroup>                
            </div>
        )
    }
}



