import * as React from 'react';
import { FormGroup } from "../components/pretty/formgroup";

class NewCrawlProps{

}

class NewCrawlState{

}

export class NewCrawl extends React.Component<NewCrawlProps, NewCrawlState>{

    render(){
        return(
            <div>
                <h1>Add new crawl</h1>
                <FormGroup id="crawl-name" label="Crawl name" type="text"></FormGroup>
                <FormGroup id="crawl-location" label="location" type="text"></FormGroup>
                <FormGroup id="crawl-start-date" label="Start date" type="date"></FormGroup>
                <FormGroup id="crawl-start-time" label="Start time" type="time"></FormGroup>
                <FormGroup id="crawl-end-date" label="End date" type="date"></FormGroup>
                <FormGroup id="crawl-end-time" label="End time" type="time"></FormGroup>
                <button className="btn btn-primary">Submit</button>
            </div>
        )
    }
}


