import * as React from 'react';

class TourProps{
    params : {id: string}
}

class TourState{

}

export class Tour extends React.Component<TourProps, TourState>{

    render(){
        return(
            <div>
                <h1>Tour - {this.props.params.id}</h1>
                <button className="btn btn-primary">Sign up</button>
            </div>
        )
    }
}



