import * as React from 'react';

class DiscountProps{
    params: {id: string}
}

class DiscountState{

}

export class Discount extends React.Component<DiscountProps, DiscountState>{

    render(){
        return(
            <div>
                <h1>Discount - {this.props.params.id}</h1>
            </div>
        )
    }
}


