import * as React from 'react';
import { LiLinkWithPic } from "../components/pretty/liwithpic";
import { Clock } from "../helpers/clock";

class CrawlsProps{

}

class CrawlsState{

}

export class Crawls extends React.Component<CrawlsProps, CrawlsState>{

    render(){
        return(
                <div>
                    <h1>Upcoming book crawls</h1>
                    <ul>
                        <LiLinkWithPic img="shelf.png" title="Oxford - Summer 2017" date={Clock.now()} href="#/crawl/31253-1234234-23423">
                            To be arranged - we are not sure if it will be in oxford, yet...
                        </LiLinkWithPic>
                        <LiLinkWithPic img="shelf.png" title="London - Winter 2018" date={Clock.now()} href="#/crawl/234234-234234-234234">
                            Another opportunity for those who missed this year's amazing crawl around london!
                        </LiLinkWithPic>
                        <li>
                            <a href="#/newcrawl" className="btn btn-primary">Add</a>
                        </li>
                    </ul> 
                </div>
        )
    }
}


