import * as React from "react";
import { FormGroup } from "../components/pretty/formgroup";

class ProfileProps{
    params: {id: string}
}

class ProfileState{

}

export class Profile extends React.Component<ProfileProps, ProfileState>{

    render(){
        return(
            <div>
                <h1>Profile - {this.props.params.id}</h1>
                <FormGroup type="file" label="profile picture" id="profile-picture"></FormGroup>
                <button className="btn btn-primary">Update</button>
            </div>
        )
    }
}



