import * as React from 'react';
import { FormGroup } from "../components/pretty/formgroup";

class LoginProps{

}

class LoginState{

}

export class Login extends React.Component<LoginProps, LoginState>{

    render(){
        return(
            <div>
                <h1>Log in</h1>
                <FormGroup id="username" type="text" label="username"></FormGroup>
                <FormGroup id="password" type="password" label="password"></FormGroup>
                <div className="small-space">
                    <a href="#/forgottenpassword">I've forgotten my password</a>
                </div>                
                <div className="small-space">
                    <a href="#/register">register now</a>
                </div>
            </div>
        )
    }
}


