import * as React from 'react';
import { LiLinkWithPic } from "../components/pretty/liwithpic";

class CrawlProps{
    params: {id: string}
}

class CrawlState{

}

export class Crawl extends React.Component<CrawlProps, CrawlState>{

    render(){
        return(
            <div>   
                <h1>Crawl - {this.props.params.id}</h1>
                <h4>Discounts:</h4>
                <ul>
                    <LiLinkWithPic title="Foyles" img="shelf.png" href="#/discount/xxx">
                        50% off all books at Foyles. A dream come true!
                    </LiLinkWithPic>
                </ul>
                <div className="btn-toolbar">
                    <a className="btn btn-primary" href="#/discounts/add">Add</a>
                </div>
                <h4>Tours:</h4>
                <ul>
                    <LiLinkWithPic title="Awesome tour" img="shelf.png" href="#/tour/awesome">
                        An amazing tour of all the awesome stuff.
                    </LiLinkWithPic>
                    <LiLinkWithPic title="antiquarian book tour" img="shelf.png" href="#/tour/antiquarian">
                        A tour of all the antiquarian and rare books around.
                    </LiLinkWithPic>
                </ul>
                <div className="btn-toolbar">
                    <a className="btn btn-primary" href="#/tours/add">Add</a>                    
                </div>
            </div>
        )
    }
}


