import * as React from 'react';
import { FormGroup } from "../components/pretty/formgroup";
import {GoogleMapLoader, GoogleMap, Marker, withGoogleMap} from "react-google-maps";


class AddTourProps{
}

class AddTourState{

}

const FirstGoogleMap = withGoogleMap(props => (
  <GoogleMap
    ref={props.onMapLoad}
    defaultZoom={8}
    defaultCenter={{ lat: 51.5, lng: 0 }}
    onClick={props.onMapClick}>
    {props.markers.map((marker, index) => (
      <Marker
        {...marker}
        onRightClick={() => props.onMarkerRightClick(index)}
      />
    ))}
  </GoogleMap>
));

export class AddTour extends React.Component<AddTourProps, AddTourState>{

    render(){
        return(
            <div>
                <h1>Add Tour</h1>
                <FormGroup id="tour-name" label="Tour name:" type="text"></FormGroup>
                <FirstGoogleMap
                    containerElement={
                    <div style={{ height: `100%`, maxHeight: "300px"}} />
                    }
                    mapElement={
                    <div style={{ height: `100%` }} />
                    }
                    markers={[]}
                />
            </div>
        )
    }
}


