import * as React from 'react';
import { FormGroup } from "../components/pretty/formgroup";

class AddDiscountProps{

}

class AddDiscountState{

}

export class AddDiscount extends React.Component<AddDiscountProps, AddDiscountState>{

    render(){
        return(
            <div>
                <h1>Add discount</h1>
                <FormGroup id="discount-location" label="where" type="text"></FormGroup>
                <FormGroup id="percent-off" label="percent off" type="number"></FormGroup>
                <FormGroup id="money-off" label="amount off" type="number"></FormGroup>
            </div>
        )
    }
}



