import * as React from 'react';
import { ClockDate } from "../../helpers/clock";

class LiWithPicProps{
    img: string;
    title: string;
    date?: ClockDate;
    href: string;
}

class LiWithPicState{

}

export class LiLinkWithPic extends React.Component<LiWithPicProps, LiWithPicState>{

    getShortDate(){
        return this.props.date != null ? this.props.date.short() : "";
    }

    render(){
        return(
            <li className="li-link-with-pic">
                <a href={this.props.href}>
                    <div className="row text-left">
                        <div className="col-md-3 col-lg-3">
                            <img src={"img/" + this.props.img}/>
                        </div>
                        <div className="col-md-9 col-lg-9">
                            <h4>{this.props.title}</h4>
                            <i>{this.getShortDate()}</i>
                            <p>{this.props.children}</p>                
                        </div>
                    </div>
                </a>
            </li>
        )
    }
}


