export class ClockDate{
    Date: Date;

    short(){
        return this.Date.getDate() + "/" + this.Date.getMonth() + "/" + this.Date.getFullYear();
    }

    getTime(){
        return this.Date.getTime();
    }

    isBefore(date: ClockDate): boolean{
        return this.getTime() < date.getTime();
    }

    isAfter(date: ClockDate): boolean{
        return this.getTime() > date.getTime();
    }

    constructor(year: number, month: number, day: number, hour?: number, minutes?: number, seconds?: number, milliseconds?: number){
        this.Date = new Date(year, month + 1, day, hour, minutes, seconds, milliseconds);
    }

    static fromTicks(ticks: number){
        var now = new Date(ticks);
        return new ClockDate(now.getFullYear(), now.getMonth(), now.getDate(), now.getHours(), now.getMinutes(), now.getSeconds(), now.getMilliseconds());
    }
}

export class Clock{

    static now(){
        return ClockDate.fromTicks(Date.now());
    }
        
}


