import * as React from "react";
import {Router, Route, hashHistory } from 'react-router';
import {TopMenu} from './components/pretty/topmenu';
import { Home } from './pages/home';
import { NotFound } from './pages/notfound';
import { Profile } from './pages/profile';
import { Crawls } from "./pages/crawls";
import { Crawl } from "./pages/crawl";
import { NewCrawl } from "./pages/newcrawl";
import { Tour } from "./pages/tour";
import { Discount } from "./pages/discount";
import { AddDiscount } from "./pages/adddiscount";
import { AddTour } from "./pages/addtour";
import { Login } from "./pages/login";
import { Register } from "./pages/register";
import { ForgottenPassword } from "./pages/forgottenpassword";

export interface AppProps{

}

export class AppState {
    history = [];
}

export class App extends React.Component<AppProps, AppState>{
    state: AppState;
    constructor(){
        super();
        this.state = new AppState();
    }
    
    addHistory(url: string){
        var self = this;
        var newHistory = self.state.history.concat(url);
        //if(newHistory.length > 10){ newHistory.shift(); }
        this.setState({
            history: newHistory
        });
    }
    render(){
        var self = this;
        return (
            <div id="main-container">
                <TopMenu clickHandler={(url) => this.addHistory(url)} />
                <section className="container content-section text-center">
                        <div className="row">
                            <div className="col-md-2"></div>
                            <div className="col-md-8">
                                <Router history={hashHistory}>
                                    <Route path="/" component={Home} />   
                                    <Route path="login" component={Login} />
                                    <Route path="register" component={Register} />
                                    <Route path="forgottenpassword" component={ForgottenPassword} />
                                    <Route path="crawls" component={Crawls} />
                                    <Route path="newcrawl" component={NewCrawl} />
                                    <Route path="crawl/:id" component={Crawl} />
                                    <Route path="tour/:id" component={Tour} />
                                    <Route path="tours/add" component={AddTour} />
                                    <Route path="discount/:id" component={Discount} />
                                    <Route path="discounts/add" component={AddDiscount} />
                                    <Route path="profile/:id" component={Profile} />                                                             
                                    <Route path="*" component={NotFound} />
                                </Router>
                            </div>
                    </div>
                    
                </section>
            </div>
        )
    }
}


