import * as React from 'react';

class FormGroupProps{
    type: string;
    id: string;
    label: string;
}

class FormGroupState{

}

export class FormGroup extends React.Component<FormGroupProps, FormGroupState>{

    render(){
        return(
            <div className="form-group row">
                <div className="col-lg-2"></div>
                <div className="col-md-3">
                    <label htmlFor={this.props.id} className="control-label">{this.props.label + ":"}</label>                
                </div>
                <div className="col-md-10 col-lg-4">
                    <input type={this.props.type} className="form-control" id={this.props.id} placeholder={this.props.label}/>
                </div>
            </div>
        )
    }
}


